@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,300;0,400;1,700&display=swap);
header{height:48px;box-shadow:1px 4px 12px 1px #cbcbcb7d;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}header h3{font-size:24px;color:#ff8c00;margin-left:4px;letter-spacing:1px}

input{height:32px;padding:4px 12px;outline-color:#f7a037;font-size:16px;width:80%;box-sizing:border-box}

button{font-size:16px;border-radius:4px;height:32px;cursor:pointer;color:#000;padding:0 16px;border:none}

select{font-size:16px;border-radius:4px;background:#efebeb;height:30px;padding-left:4px;font-weight:700}

.results{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;margin:2rem 4rem}.results .tools{display:-webkit-flex;display:flex;width:80%;-webkit-justify-content:center;justify-content:center}.results .tools .search input{width:60%}.results .tools .sort{margin-left:1rem}.results .repos{width:100%;-webkit-justify-content:center;justify-content:center}.results .row{display:-webkit-flex;display:flex;margin:2rem 0;padding:12px 8px;border-radius:4px;box-shadow:1px 6px 11px lightgrey}.results .row .owner{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;margin-left:1rem;width:30%}.results .row .owner a{font-size:18px;font-weight:700;color:#000}.results .row .owner img{height:100px;width:100px;border-radius:300px}.results .row .info{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-between;justify-content:space-between;width:60%}.results .row .info ul{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-between;justify-content:space-between}.results .row .info li:first-child a{font-size:18px;text-align:center}.results .row .info li{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}.results .row .info li svg{margin-right:6px}@media (max-width: 625px){.results{margin:1rem}.results .row>.owner img{height:80px;width:80px}}@media (max-width: 800px){.results .tools{-webkit-flex-direction:column;flex-direction:column;width:80%}.results .tools .search input{font-size:14px}.results .tools .search select{width:calc(100% - 60%)}.results .tools .sort{margin-left:0;margin-top:1rem}}@media (min-width: 600px){.results{margin:2rem 4rem}.results .repos{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;-webkit-justify-content:space-evenly;justify-content:space-evenly}.results .repos .row{min-width:300px;width:300px;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center}.results .repos .row .info{width:85%;margin-top:8px;padding:0 8px}}

.loading{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-between;justify-content:space-between;margin:2rem}.loading p{font-size:16px;margin-left:8px}.loading svg{-webkit-animation:grow 700ms ease-in infinite;animation:grow 700ms ease-in infinite}@-webkit-keyframes grow{0%{height:28px;width:28px}100%{height:36px;width:36px}}@keyframes grow{0%{height:28px;width:28px}100%{height:36px;width:36px}}

.main{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.main form{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;margin-top:24px}.main form input{width:60%;margin-right:4px}@media (max-width: 576px){.main form input{width:66%}}

*{margin:0}body{font-family:'Lato', sans-serif}li{list-style-type:none;padding-left:0}a{text-decoration:none}

